import { AjaxHttp } from '../../../js/ajaxHttp'
import { getYMDFormateTime, isNullValue } from "../../../js/utility.js"

export default {
  name: 'articledetails',
  components: {},
  computed: {
    update_time() {
      return getYMDFormateTime(this.detail.update_time);
    }
  },
  data() {
    return {
      show: false,
      detail: {}
    }
  },
  mounted() {
    this.getDetail();
  },
  methods: {
    getDetail() {
      AjaxHttp('POST', 'm=announcement&a=view', { 'id': this.$route.params.id }, this, function(res, wself, success) {
        if (success) {
          wself.detail = res.data;
          wself.show = true;
        }
      });
    },
    enterUser() {
      if (!isNullValue(this.detail.role_id)) {
        this.$router.push({
          name: 'user',
          params: {
            id: this.detail.role_id
          }
        })
      }
    }
  }
}




